<template>
  <div>
    <div class="search_container" v-loading="submitLoading">
      <el-form :inline="true" :model="params" class="demo-form-inline" ref="filterFormRef">
        <el-form-item label="财务组织" size="mini">
          <!-- companyListInvok -->
          <el-select clearable v-model="params.company_id" placeholder="请选择" @focus="companyListFun" @change="companyChange" filterable>
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': ''">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="支付单据生成时间" size="mini">
          <el-date-picker v-model="PaymentTime" type="daterange" @change="PaymentTimeChangeDate" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="付款凭证提交时间" size="mini">
          <el-date-picker v-model="ProofTime" type="daterange" @change="PaymentTimeChangeDate" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="客户名称" size="mini">
          <el-select v-model="params.customer_id" filterable placeholder="请输入" clearable @focus="clienteleListInvok" @change="getCustomerChange" :loading="loading" remote :remote-method="clienteleListInvok">
            <ul class="customer customer-ul4 customer-ul2">
              <li>客户编号</li>
              <li>客户名称</li>
              <li>联系人</li>
              <li>联系电话</li>
              <li>区域</li>
            </ul>
            <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
              <ul class="customer customer-ul4">
                <li :title="item.customer_id">{{ item.customer_id }}</li>
                <li :title="item.customer_name">{{ item.customer_name }}</li>
                <li :title="item.contact_name">{{item.contact_name}}</li>
                <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
              </ul>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="商城订单号" size="mini">
          <el-input v-model="params.order_sn" placeholder="请输入" @keyup.enter.native='onSearch(1)'></el-input>
        </el-form-item>
        <el-form-item label="支付单据号" size="mini">
          <el-input v-model="params.pay_sn" placeholder="请输入" @keyup.enter.native='onSearch(1)'></el-input>
        </el-form-item>
        <el-form-item label="订单来源" size="mini">
          <el-select clearable v-model="params.order_from">
            <el-option label="大宗" :value="1">大宗</el-option>
            <el-option label="服务站" :value="5">服务站</el-option>
            <el-option label="车郎中" :value="6">车郎中</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="收款状态" size="mini">
          <el-select clearable v-model="params.pay_state">
            <el-option label="待付款" :value="0">待付款</el-option>
            <el-option label="待确认" :value="1">待确认</el-option>
            <el-option label="拒审" :value="2">拒审</el-option>
            <el-option label="已确认" :value="3">已确认</el-option>
          </el-select>
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch()" size="mini" type="primary">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="add_delet">
      <el-checkbox v-model="checked" @change="CommissionAmount">公户待销金额>0</el-checkbox>
      <xTable v-if='tableColumn.length > 0' :ajax='{ url: "getServiceCollectionList", params: params }' ref='xTable' :footerMethod='footerMethod' @getData='dealData' :tableColumn='tableColumn' />
    </div>
    <vxe-modal v-model="modeStact" show-footer mask-closable ref="model">
      <p>应付总金额：{{AmountTotal}}</p>
      <xTable v-if='modeStact' :isPage='false' :ajax='{ url: "service_getOrderDetailed", params: {pay_sn:Securitypay_sn},result:"data.data.order_list" }' @getData='modeStactDate' ref='xTablemodel' :tableColumn='modeltableColumn' />
      <template #footer>
        <div style="text-align: center;">
          <vxe-button type="submit" status="primary" @click="modeStact=false">关闭</vxe-button>
        </div>
      </template>
    </vxe-modal>
  </div>
</template>

<script>
// 线下付款收款管理
import { tableHeader } from "./config/index";
export default {
  mixins: [tableHeader],
  data() {
    return {
      params: {},
      modeStact: false,
      Securitypay_sn: null,
      timeValue: [],
      PaymentTime: [],
      AmountTotal: 0,
      ProofTime: [],
      checked: false,
    };
  },
  methods: {
    onSearch() {
      this.params = this.$publicFution.dealParams(this.params);
      this.$refs.xTable._getData(1);
    },
    CommissionAmount(e) {
      this.params.is_common_balance = e ? 1 : 0;
      this.$refs.xTable._getData(1);
    },
    onReset() {
      this.params = {};
      this.PaymentTime = this.ProofTime = [];
      this.$refs.xTable._getData(1);
    },
    modeStactDate(val) {
      this.AmountTotal = val.data.data.pay_price;
    },
    PaymentTimeChangeDate() {
      if (this.PaymentTime) {
        this.params.start_add_time = this.PaymentTime[0];
        this.params.end_add_time = this.PaymentTime[1];
      } else {
        this.params.start_add_time = this.params.end_add_time = "";
      }
      if (this.ProofTime) {
        this.params.start_payment_time = this.ProofTime[0];
        this.params.end_payment_time = this.ProofTime[1];
      } else {
        this.params.start_payment_time = this.params.end_payment_time = "";
      }
    },
    companyListFun() {
      this.$api.getVisibleCompany().then(({ data }) => {
        if (data.code == 200) {
          this.$axMethod
            .getCompanyListDefalut({
              company_id: data.data,
            })
            .then((res) => {
              this.companyList = res.data.data.company_list;
            });
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
/deep/.vxe-cell {
  .imgs {
    width: 33px;
    height: 20px;
  }
}
</style>