
export const tableHeader = {
  data() {
    return {
      status: true,
      tableColumn: [
        {
          title: '支付单据号',
          field: 'pay_sn',
          width: 130
        },
        {
          title: '操作',
          width: 150,
          fixed: "right",
          align: "center", // 列对齐方式
          slots: {
            default: (scope, h) => {
              var a = []
              let buttonConfirm = scope.row.operator_button;
              if (buttonConfirm.confirm_show == 1 || buttonConfirm.confirm_show == 2) {
                a.push(<el-button size="small" type="text" disabled={buttonConfirm.confirm_show == 2 ? true : false} onClick={() => this.ConfirmGoods(scope.row)} > 确认收款 </el-button>)
              }
              if (buttonConfirm.refuse_show == 1 || buttonConfirm.refuse_show == 2) {
                a.push(<el-button size="small" type="text" disabled={buttonConfirm.refuse_show == 2 ? true : false}
                  onClick={() => this.RefusalJustice(scope.row)}  > 拒审  </el-button>)
              }
              return [
                a
              ]
            }
          }
        },
        {
          field: 'order_from_name',
          width: 156,
          title: '订单来源'
        },
        {
          field: 'pay_state_name',
          width: 150,
          title: '收款状态'
        },
        {
          field: 'customer_name',
          width: 150,
          title: '客户名称'
        },
        {
          field: 'store_name',
          width: 150,
          title: '订单所属店铺'
        },
        {
          title: '商城订单明细',
          field: 'department_name',
          width: 100,
          align: "center", // 列对齐方式
          slots: {
            default: (scope, h) => {
              var a = []
              a.push(<el-link type="primary" onClick={() => { this.seeDepartOrder(scope.row) }}>查看</el-link>)
              return a
            }
          }
        },
        {
          field: 'pay_account',
          width: 150,
          title: '付款账户名'
        }, {
          title: '收款开户行',
          field: 'account_bank',
          width: 100
        },
        {
          title: '收款开户名',
          field: 'account_name',
          width: 150
        },
        {
          title: '收款账号',
          field: 'account_number',
          width: 84,
        },
        {
          title: '应付总金额',
          field: 'pay_price',
          width: 100,
        },
        {
          title: '公户待销金额',
          field: 'account_balance',
          width: 150
        },
        {
          title: '付款凭证',
          field: 'pay_pic_url',
          width: 150,
          slots: {
            default: (scope, h) => {
              return scope.row.pay_pic_url.map(item => {
                return <el-image src={item} class="imgs" onClick={(event) => { this.handleClickItem(event) }} preview-src-list={[item]} />
              })
            }
          }
        },
        {
          title: '实收金额',
          field: 'collection_price',
          width: 110,
          slots: {
            default: (scope, h) => {
              if (scope.row.order_pay_type == 2 && scope.row.pay_state == 1) {
                return [<el-input v-model={scope.row.collection_price} onInput={() => this.linmikNumber(scope.row)} placeholder="请输入"></el-input>]
              } else {
                return [<span>{scope.row.collection_price}</span>]
              }
            }
          }
        },
        {
          title: '充值金额',
          field: 'recharge_price',
          width: 160
        },
        {
          title: '到账类型',
          field: 'arrival_type_name',
          width: 120,
        },
        {
          title: '操作人',
          field: 'operator_user_name',
          width: 120
        },
        {
          title: '操作时间',
          field: 'operator_time',
          width: 120
        }
      ],
      modeltableColumn: [
        {
          title: '商城订单号',
          field: 'order_sn',
        },
        {
          title: '商品（SKU）数',
          field: 'goods_num',
        },
        {
          title: '金额（元）',
          field: 'order_amount',
        }
      ]
    }
  },
  methods: {
    seeDepartOrder(row) {
      this.Securitypay_sn = row.pay_sn;
      this.$nextTick(() => {
        this.modeStact = true;
      })
    },
    linmikNumber(row) {
      if (Number(row.collection_price) > Number(row.pay_price)) {
        this.$set(row, 'recharge_price', Number(row.collection_price) - Number(row.pay_price))
      } else {
        this.$set(row, 'recharge_price', 0)
      }
    },
    confirmDilog(content, val, state) {
      this.$confirm(content, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        if (state) {
          this.$api.service_auditorPass({ collection_price: val.collection_price, pay_sn: val.pay_sn }).then(({ data }) => {
            if (data.code == 200) {
              this.onSearch()
              this.$confirm.close()
            }
          })
        }

      })
    },
    ConfirmGoods(val) {
      if (val.order_pay_type == 2 && Number(val.collection_price) <= 0) {
        this.$message.error('实收金额不能为空');
        return
      }
      let content;
      if (val.order_pay_type == 1) {//对公
        if (Number(val.account_balance) >= Number(val.pay_price)) {
          content = `客户账户待销金额:${val.account_balance}，本次收款金额:${val.pay_price}，是否确认收款？`
          this.confirmDilog(content, val, true)
        } else {
          content = `当前单据应付总金额>公户待销金额，不支持确认收款`
          this.confirmDilog(content, val, false)
        }
      } else {
        if (val.recharge_price >= 0) {
          content = `当前单据实收金额:${val.collection_price}，支付单据金额 ${val.pay_price}，将有${val.recharge_price}元充值进入客户余额`
          this.confirmDilog(content, val, true)
        } else {
          content = `当前单据余额充值金额<0，不支持确认收款`
          this.confirmDilog(content, val, false)
        }
      }
    },
    RefusalJustice(val) {
      this.$prompt('请输入拒审原因', '拒审', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /\S/,
        inputErrorMessage: '拒审原因不能为空'
      }).then(({ value }) => {
        this.$api.service_auditorRefuse({ pay_sn: val.pay_sn, auditor_reason: value }).then(({ data }) => {
          if (data.code == 200) {
            this.$message.success(data.msg);
            this.$refs.xTable._getData();
          }
        })
      })
    },
    handleClickItem(e) {
      e.stopPropagation()
      this.$nextTick(() => {
        let domImageMask = document.querySelector(".el-image-viewer__mask");  // 获取遮罩层dom
        console.log(domImageMask)
        if (!domImageMask) {
          return;
        }
        domImageMask.addEventListener("click", () => {
          // 点击遮罩层时调用关闭按钮的 click 事件
          document.querySelector(".el-image-viewer__close").click();
        });
      })
    }
  },
}